@import url(https://fonts.googleapis.com/css?family=Paytone+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Courgette&display=swap);
@import url(https://fonts.googleapis.com/css?family=Paytone+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #352793;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
/* colors */
/* fonts */
/* sizes */
.App {
  text-align: center;
}

/* global classes */
.title-font {
  font-family: "Paytone One", sans-serif;
  line-height: 130%;
}

.title-font-2 {
  font-family: "Nunito", sans-serif;
  line-height: 130%;
}

.title-font-3 {
  font-family: "Courgette", cursive;
  line-height: 130%;
}

.title-font-size {
  font-size: 100px;
}

.section-title {
  padding-top: 7%;
}

/* --------------------- Banner --------------------- */
.banner {
  color: white;
  margin-top: 60px;
}

.banner-image-mobile {
  display: none;
}

.banner-image {
  width: 90%;
}

.banner-text {
  margin-bottom: 30px;
}

.banner-text h1 {
  font-size: 62px;
}

.banner-text .text-yellow {
  color: #7ced03;
}

.banner-text .text-customer {
  color: rgb(132, 220, 255);
}

/*----------------------About us area--------------------------*/
.about-us-area {
  background: #130f40 no-repeat;
  background-image: url(http://127.0.0.1:5500/static/media/shape2.4164b7fa.png);
  background-position: center;
  background-size: 500px;
  padding: 5%;
}
.about-us-area .about-us-text-area {
  padding: 3em;
  background: #0c2461;
  box-shadow: 0px 0px 46px -17px rgba(0, 0, 0, 0.75);
}
.about-us-area .about-us-text-area h2 {
  font-weight: 900;
}

/*---------------------- portfolio area --------------------------*/
.portfolio {
  padding: 5%;
}
.portfolio .portfolio-title {
  padding: 0px 0px 40px 0px;
}
.portfolio .portfolio-title h1 {
  font-size: 100px;
}
.portfolio .portfolio-title p {
  font-size: 1.6rem;
}
.portfolio .portfolio-thumb {
  margin: 0 auto;
  margin-bottom: 9%;
  /* width: 100%;
      height: 100%; */
  float: left;
  overflow: hidden;
  position: relative;
  text-align: center;
  cursor: default;
}
.portfolio .portfolio-thumb img {
  width: 100%;
  transition: transform 0.25s, visibility 0.25s ease-in;
}
.portfolio .portfolio-thumb :hover img {
  transform: scale(1.5);
}
.portfolio .portfolio-thumb .image-container .hover-content {
  width: 100% !important;
  height: 100% !important;
  background: #352793;
}
.portfolio .portfolio-thumb .image-container .title {
  color: white;
  opacity: 1;
  margin-top: 40%;
  font-weight: 900;
  font-size: 30px;
  font-family: "Nunito", sans-serif;
}
.portfolio .portfolio-thumb .image-container .hover-content.fadeIn {
  opacity: 0.9;
}

/*---------------------- lead area --------------------------*/
.lead {
  background: #130f40;
}
.lead h1 {
  font-size: 100px;
}
.lead button {
  border-radius: 7px 7px 7px 7px;
  -moz-border-radius: 7px 7px 7px 7px;
  -webkit-border-radius: 7px 7px 7px 7px;
  border: 0px solid #000000;
  height: 50px;
  width: 50%;
  background: #352793;
  font-family: "Nunito", sans-serif;
  font-weight: 900;
  font-size: 100%;
}
.lead button :hover {
  text-decoration: none;
}
.lead button a {
  color: white;
}

/*---------------------- footer area --------------------------*/
footer {
  background: #130f40;
}

/*---------------------------------------------------------------*/
/*---------------------- testimonial page ---------------------------*/
/*---------------------------------------------------------------*/
.testimonial {
  padding: 10%;
  background: #130f40;
}
.testimonial .title-font {
  color: white;
}
.testimonial p {
  font-size: 1.6rem;
  color: white;
}
.testimonial h5 {
  color: white;
}

/*---------------------------------------------------------------*/
/*---------------------- contact page ---------------------------*/
/*---------------------------------------------------------------*/
.contact p {
  font-size: 1.6rem;
}
.contact .contact-form {
  padding-bottom: 10%;
}
.contact .contact-form form-group:focus {
  outline: none;
}
.contact .contact-form input,
.contact .contact-form textarea {
  background: transparent;
  font-family: "Nunito", sans-serif;
  border: 1px solid white;
  padding: 4%;
  color: white;
  font-weight: 600;
}
.contact .contact-form input::placeholder,
.contact .contact-form textarea::placeholder {
  color: white;
  opacity: 0.5;
}
.contact .contact-form input:focus,
.contact .contact-form textarea:focus {
  outline: none;
  box-shadow: none;
}
.contact .contact-form button {
  border-radius: 7px 7px 7px 7px;
  -moz-border-radius: 7px 7px 7px 7px;
  -webkit-border-radius: 7px 7px 7px 7px;
  border: 0px solid #000000;
  height: 50px;
  width: 50%;
  background: #1d4ad1;
  font-family: "Nunito", sans-serif;
  color: white;
  font-weight: 900;
  font-size: 100%;
}
.contact .contact-form button :hover {
  text-decoration: none;
}
.contact .contact-form button :focus {
  outline: white;
  box-shadow: white;
}

/*---------------------------------------------------------------*/
/*---------------------- services page ---------------------------*/
/*---------------------------------------------------------------*/
.services .services-title {
  padding-bottom: 5%;
}
.services .title-text {
  font-size: 1.6rem;
}
.services .service-name {
  font-weight: 900;
  font-size: 25px;
}
.services .service-boxes {
  padding: 10%;
}
.services .service-boxes .service-icon {
  position: absolute;
  right: 90%;
}
.services .service-boxes img {
  width: 90px;
}
.services .service-boxes .service-info-title {
  height: 25px;
  margin-bottom: 5%;
}
.services .service-boxes .service-description {
  font-size: 20px;
}

/*---------------------------------------------------------------*/
/*--------------- portfolio detail page --------------*/
/*---------------------------------------------------------------*/
.detail .services-title {
  padding-bottom: 5%;
}
.detail .detail-image {
  width: 100%;
}
.detail .detail-info .work-title {
  font-weight: 900;
}
.detail .detail-info .description {
  line-height: 1.6rem;
  font-size: larger;
}
.detail .detail-info hr {
  border-color: white;
  border-style: solid;
}

/*---------------------------------------------------------------*/
/*--------------- team page --------------*/
/*---------------------------------------------------------------*/
.team {
  background: #130f40;
}
.team .portfolio-thumb {
  margin: 0 auto;
  margin-bottom: 5%;
  /* width: 100%;
      height: 100%; */
  float: left;
  overflow: hidden;
  position: relative;
  text-align: center;
  cursor: default;
}
.team .portfolio-thumb img {
  width: 100%;
  transition: transform 0.25s, visibility 0.25s ease-in;
}
.team .portfolio-thumb :hover img {
  transform: scale(1.5);
}
.team .portfolio-thumb .image-container .hover-content {
  width: 100% !important;
  height: 100% !important;
  background: #352793;
}
.team .portfolio-thumb .image-container .title {
  color: white;
  opacity: 1;
  margin-top: 30%;
  font-weight: 900;
  font-size: 30px;
  font-family: "Nunito", sans-serif;
}
.team .portfolio-thumb .image-container .hover-content.fadeIn {
  opacity: 0.9;
}
.team p {
  font-size: 1.6rem;
}
.team .person-info {
  visibility: visible;
  display: none;
}

/* for mobile phones (portrait) */
@media (min-width: 320px) and (max-width: 480px) {
  /* global styles */
  .section-title {
    padding-top: 0%;
  }
  /* global styles end */
  .title-font-size {
    font-size: -webkit-xxx-large;
  }
  .banner {
    padding: 0px 0 180px;
  }
  .banner-image-desktop {
    display: none;
  }
  .banner-image-mobile {
    display: block;
  }
  .banner-text {
    padding-top: 10%;
  }
  .banner-text h1 {
    font-size: 48px;
    margin-bottom: 8%;
    padding: 4%;
  }
  .banner-text .title-font-2 {
    font-size: 48px;
    line-height: 148% !important;
  }
  .banner-image {
    width: 90%;
  }
  /*----------------------About us area--------------------------*/
  .about-us-area {
    background-size: 400px;
  }
  .about-us-area .about-us-text-area h2 {
    font-size: 25px;
  }
  /*----------------------portfolio area--------------------------*/
  .portfolio .portfolio-title {
    padding: 0;
    padding-top: 5%;
  }
  .portfolio .portfolio-title h1 {
    font-size: -webkit-xxx-large;
  }
  /*----------------------lead area--------------------------*/
  .lead h1 {
    font-size: -webkit-xxx-large;
  }
  /*----------------------contact page--------------------------*/
  .contact p {
    font-size: 1.6rem;
  }
  /*----------------------services page--------------------------*/
  .services .services-title {
    padding-bottom: 0;
  }
  .services .service-boxes .service-icon {
    position: unset;
  }
  .services .service-boxes .service-info-title {
    height: 50px;
    margin-bottom: 5%;
  }
  .services .service-boxes .service-info-title .service-name {
    font-size: 20px;
  }
  .services .service-boxes .service-description {
    text-align: center !important;
    font-size: 15px;
  }
  .services .title-text {
    font-size: 1.6rem;
  }
  /*----------------------team page--------------------------*/
  .team .person-info {
    visibility: visible;
    display: inline;
  }
  .team .person-info .person-position {
    font-size: 15px;
    margin-bottom: 15%;
  }
}
