.header-transparent {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}
.header-transparent .navbar-brand {
  color: white;
  font-weight: 900;
}
.header-transparent span i {
  color: white;
  padding-left: 50%;
}
.header-transparent .navbar-toggler:focus {
  outline: none !important;
}
.header-transparent .navbar-collapse.show,
.header-transparent .collapsing {
  background: #352793;
}
.header-transparent nav.navbar ul li a.nav-link {
  color: white;
  padding: 1rem;
  border-bottom: 1px solid #1d4ad1;
}

/* for mobile phones (portrait) */
@media (min-width: 320px) and (max-width: 480px) {
  .header-transparent .navbar {
    padding-top: 5%;
